.additional__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.additional__title {
  margin-top: 25px;
  margin-bottom: 36px;
}

.additional__link {
  text-decoration: none;
}

.additional__sub-title {
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
  color: #222222;
  margin: 13px 0;
}

.additional__items {
  margin-bottom: 15px;
  width: 100%;
}

.additional__button {
  width: 100%;
}

.additional__criterion {
  text-align: start;
}

.additional__meaning {
  text-align: end;
}
